import React from 'react';
import './variant.css';
import { Button } from '../../Buttons/Button';

export const Variant = ({ variantImg, name }) => {
    return (
        <div className="variant-card">
            <img src={variantImg} alt="" />
            <h4>{name}</h4>
            <Button className='btn-violet' text='Заказать' />
        </div>
    );
}
