import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ScrollableAnchor from 'react-scrollable-anchor';
import './order.css';
import { Button } from '../Buttons/Button';

export const Order = () => {
    return (
        <ScrollableAnchor id={'order'}>
            <section className="order">
                <Container>
                    <Row>
                        <Col lg={6}>
                            <h2>Как заказать?</h2>
                            <p> Для заказа оригинальных автомобильных ковриков, заполните простую форму справа.
 </p>
                            <br />
                            <p>*Оплата при получении. Доставка для розничных клиентов осуществляется курьерскими службами по всей России и оплачивается отдельно (300 рублей). Условия доставки для партнеров уточняйте у представителей КоврикиPro</p>
                        </Col>
                        <Col lg={6}>
                            <form className='form' action="">
                                <div className="product-data">
                                    <h4>Данные товара</h4>
                                    <select name="kovrikk">
                                        <option selected disabled value="value2">Цвет коврика</option>
                                        <option value="value1">Красный</option>
                                        <option value="value3">Значение 3</option>
                                    </select>
                                    <select name="okontovka">
                                        <option selected disabled value="value2">Цвет оконтовки</option>
                                        <option value="value1">Красный</option>
                                        <option value="value3">Значение 3</option>
                                    </select>
                                </div>
                                <div className="user-data">
                                    <h4>Контактные данные</h4>
                                    <input placeholder="Ваше имя" type="text" />
                                    <input placeholder="Номер телефона" type="number" />
                                    <input placeholder="Адрес доставки" type="text" />
                                </div>
                                <Button className='btn-violet btn-big' text='Отправить заявку' />
                            </form>
                        </Col>
                    </Row>
                </Container>
            </section>
        </ScrollableAnchor>
    );
}
