import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { Button } from '../Buttons/Button';
import logo from '../../images/logo.svg';
import headerImg from '../../images/eva 1.svg';
import './header.css';

export const Header = () => {
    return (
        <header>

            <Container>

                <Row>
                    <div className="top-line">
                        <Col lg={2}>
                            <img className="logo" src={logo} alt="" />
                        </Col>
                        <Col lg={9}>
                            <nav className="menu">
                                <ul>
                                    <li> <a href="#features">Преимущества</a> </li>
                                    <li> <a href="#variants">Популярные товары</a> </li>
                                    <li> <a href="#order">Как заказать?</a> </li>
                                    <li> <a href="#contacts">Контакты</a> </li>

                                </ul>
                            </nav>
                        </Col>
                        <Col lg={3}>
                            <div className="right">
                                <Button className='btn-lite' text='Заказать'></Button>
                            </div>
                        </Col>
                    </div>
                </Row>

                <div className="header-center">
                    <Row>
                        <Col lg={6}>
                            <h1>Оригинальные автомобильные коврики</h1>
                            <p>Изготовлены из инновационного матерала</p>
                            <Button className='btn-violet' text='Подробнее' />
                        </Col>
                        <Col lg={6}>
                            <img src={headerImg} alt="" />
                        </Col>
                    </Row>
                </div>
            </Container>


        </header >
    );
}