import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ScrollableAnchor from 'react-scrollable-anchor';
import { Variant } from './Variant/Variant';
import one from '../../images/header-img2.png';
import './variants.css';

export const Variants = () => {
    return (
        <ScrollableAnchor id={'variants'}>
            <section className="product-variants">
                <Container>
                    <div className="title">
                        <h2>Популярные варианты</h2>
                    </div>
                    <Row>
                        <Col lg={3}> <Variant variantImg={one} name='Алые макасинки' /> </Col>
                        <Col lg={3}> <Variant variantImg={one} name='Алые макасинки' /> </Col>
                        <Col lg={3}> <Variant variantImg={one} name='Алые макасинки' /> </Col>
                        <Col lg={3}> <Variant variantImg={one} name='Алые макасинки' /> </Col>
                    </Row>
                </Container>
            </section>
        </ScrollableAnchor>
    );
}
