import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ScrollableAnchor from 'react-scrollable-anchor';
import { Feature } from './Feature/Feature';
import invest from '../../images/invest.svg';
import good from '../../images/good.svg';
import truck from '../../images/truck.svg';
import './features.css';

export const Features = () => {
    return (
        <ScrollableAnchor id={'features'}>
            <section className="features">
                <Container>
                    <Row>
                        <Col lg={3}>
                            <Feature icon={invest} description="Оплата при получении" />
                        </Col>
                        <Col lg={3}>
                            <Feature icon={good} description="Все лекала сняты нами (не из интернета)" />
                        </Col>
                        <Col lg={3}>
                            <Feature icon={truck} description="Доставка в любой город 300 рублей за 2-3 дня" />
                        </Col>
                        <Col lg={3}>
                            <Feature icon={good} description="Коврики лягут идеально, как влитые" />
                        </Col>
                    </Row>
                </Container>
            </section>
        </ScrollableAnchor>
    );
}